import { Component, OnInit } from '@angular/core';

import { Listing } from '../../shared/listing';

import { QuestionsService } from "../questions.service";

@Component({
  selector: 'app-listing',
  templateUrl: './listing.component.html',
  styleUrls: ['./listing.component.css']
})
export class ListingComponent extends Listing implements OnInit {
  language: any ;
  queryString: any = {} ;
  public sortBy   = "question" ;
  public sortOrder = "asc" ;
  constructor(
    private klassService: QuestionsService ) { super(); }

  ngOnInit() {
    this.language = 'en';
    this.getItems();
  }

  getItems() {
    this.items = [];
    this.busy = this.klassService.getXItems(this.filterParams, this.language, 'allQuestion').then((res) => {
      if(!res)
        return true;

      this.items = res.data.filter( _item => _item.is_deleted != true);
      this.tmpItems = this.items;
      this.itemsTotal = this.items.length;
    });
  }

  public filterItems() {
    if (this.tmpItems.length == 0)
      this.tmpItems = this.items;
    this.items = this.tmpItems.filter(_item =>
       _item.question.toLowerCase().lastIndexOf(this.searchTerm.trim().toLowerCase()) != -1
    );
  }

  removeItem() {
    this.closeModal();
    this.item.is_deleted = true;
    this.busy = this.klassService.removeItem(this.item, this.language, 'question').then((res) => {
      if(!res)
        return true;
      this.item = null;
      this.getItems();
    });
  }

  toggleStatus(_item) {
    let id = _item._id ;
    let obj = {_id :_item._id, is_active: _item.is_active }; 
    this.busy = this.klassService.toggleStatus(obj, this.language, "question/changeStatus").then((res) => {
        if(!res) 
         return true;

      console.log(res.msg);
    });
  }



}



