import { Component, OnInit, ViewChild } from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';

import { QuestionsService } from "../questions.service";

import { NgxIntlTelInputModule } from 'ngx-intl-tel-input';
import { environment } from '../../../environments/environment';
import { DomSanitizer } from '@angular/platform-browser';
import { BaseForm } from '../../shared/base-form';

@Component({
  selector: 'app-form',
  providers: [],
  templateUrl: './form.component.html',
  styleUrls: ['./form.component.css']
})
export class FormComponent extends BaseForm  implements OnInit {
  question: any;
  language: any;
  url: string ;

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private klassService: QuestionsService,
    private sanitizer: DomSanitizer
  ) { super(); }

  ngOnInit() {
    this.language = 'en';
    // subscribe to router event
    this.activatedRoute.params.subscribe((params: Params) => {
        this.itemID = params['id'];
        if(this.itemID){
          this.isEditable = true;
          this.getItem();
        }
      });
  }

  getItem() {
    this.busy = this.klassService.getItem(this.itemID, this.language).then((res) => {
      if(!res)
        return true;
      this.item = res.data;
    });
  }

  createItem() {

    this.busy = this.klassService.addItem(this.item, this.language, 'question')
    .then((res) => {
      if(!res)
        return true;

      this.item = res;
      //this.router.navigate(['/questions']);
    });
  }

  updateItem() {
 
    this.busy = this.klassService.updateItem(this.itemID, this.item, this.language, 'question').then((res) => {
      if(!res)
        return true;
      //this.form.form.markAsPristine();
    });
  }

  setFiles(event, side ) {
    if(side === 'left'){
      this.item.l_Image = event.target.files[0];
    }
    if(side === 'right'){
      this.item.r_Image = event.target.files[0];
    }
    let l = '';
    let r = '';
    let reader = new FileReader();
    reader.onload = (e: any) => {
      let _self = self;
    if ( side === 'left') {
      l = e.target.result;
    }
    if ( side === 'right') {
      r = e.target.result;
    }
  }
    reader.readAsDataURL(event.target.files[0]);
    // setTimeout(function() {
    //   if(l)this.leftPreview = l;
    //   if(r)this.rightPreview = r;
    //   console.log(this.leftPreview, side)
    // }, 500);
    this.markDirty();
  }


}



